<div class="main_content" :class="[color]"><div class="status_checkbox border_color" :class="{active: completed, bg_color: completed}" @click.prevent="completedEvent()"><svgWrapper class="check" v-if="completed === true" icon="check" :class="[color]"></svgWrapper></div><strong class="title" :class="{completed: completed}">{{ title }}</strong><button class="toggle_child" v-if="childLength &gt; 0" :class="{active: childView}" @click.prevent="childViewEvent()"><svgWrapper class="icon" icon="arrow"></svgWrapper></button></div>