<div class="wrapper" :class="{group_task: isGroupTask}"><div class="task_wrapper"><Main :title="task.title" :completed="task.completed" :completedEvent="updateCompletedStatus" :childLength="childAllTodos.length" :childView="viewChildTodos" :childViewEvent="toggleViewChildTodos" :color="color"></Main><Additional :completed="task.completed" :targetDate="task.target_date" :childLength="childAllTodos.length" :childCompletedLength="childCompletedTodos.length" :childViewEvent="toggleViewChildTodos"></Additional></div><div class="task_child_wrapper" v-if="viewChildTodos"><ChildTask class="child" v-for="(childTask, key) in childAllTodos" :key="key" :childTask="childTask" :color="color"></ChildTask></div></div>