<div class="task_wrapper" :class="[color]"><div class="main_content"><div class="status_checkbox border_color" :class="{active: childTask.completed, bg_color: childTask.completed}" @click.prevent="updateCompletedStatus()"><svgWrapper class="check" v-if="childTask.completed === true" icon="check" :class="[color]"></svgWrapper></div><strong class="title" :class="{completed: childTask.completed}">{{ childTask.title }}</strong></div></div>