<div class="additional_content"><div class="element child_counter" v-if="childLength &gt; 0" @click.prevent="childViewEvent()"><svgWrapper class="icon" icon="structure"></svgWrapper><strong class="label">{{ childCompletedLength }}/{{ childLength }}</strong></div><div class="element target_day" v-if="targetDate !== null" :class="{red: (isPastDate(targetDate) &amp;&amp; completed === false), grey: completed}"><svgWrapper class="icon" icon="calendar"></svgWrapper><strong class="label">{{ DateFormat(targetDate, false) }}</strong></div></div>